nav {
    
    display: block;
    position: absolute;
    width: 100%;
  
    text-align: center;
    bottom: 5rem;
}

.scroll {
    margin-top: 0.7rem;
    color: var(--color-light);
 
}

nav a {
    padding: 1rem;
    background: transparent;
    border-radius: 50%;
    display: flex;
    color: var(--color-primary);
    font-size: 5rem;
    justify-content: center;
}

nav a :hover {
    opacity: 0.5;
    color: var(--color-white);   
}

@media screen and (max-width: 800px) {

    nav {
        display: none;
    }
}

