.floater {
    font-size: 2rem;
    position: fixed;
    top: 0%;
    color: var(--color-white);
    opacity: 0.4;
    margin-left: 0.5rem;
    margin-top: 0.5rem;
   
}

.container__help {
    padding: 0.5rem;
}

.help_text {
    font-size: 1rem;
    padding-right: 2rem;
    padding-left: 1rem;
}


@media screen and (max-width: 800px) {

    .floater {
        position: relative;
        text-align: right;
        font-size: 1.5rem;
    }

    .help_text {
        position: absolute;
        right: 0%;
        margin-right: 0.5rem;
    }
}